@use "sass:math";

@include body-class(true) {

    [id*="forminator-modal-addons-details-"] {

        .sui-box {

            .sui-box-header {
                display: block;
                padding-top: $sui-gutter-md !important;
                padding-bottom: $sui-gutter-md !important;

                .sui-dialog-close {
                    position: absolute;
                    top: #{math.div($sui-gutter, 2)};
                    right: #{math.div($sui-gutter, 2)};
                }

                .forminator-details-header {

                    .forminator-details-header--image {
                        width: 60px;
                        height: 60px;
                        border-radius: $border-radius;
                        background-size: cover;
                        background-repeat: no-repeat;
                        background-position: center;

                        + .forminator-details-header--data {

                            @include media(min-width, md) {
                                margin-left: $sui-gutter-md;
                            }
                        }

                        @include media(max-width, md) {
                            display: none;
                            flex: 0 0 auto;
                        }
                    }

                    .forminator-details-header--data {
                        text-align: left;

                        .forminator-details-header--title {
                            margin: 0;
                            color: #333;
                            font: 700 22px/30px $font;
                            letter-spacing: $font--letter-spacing;

                            .sui-tag {
                                vertical-align: middle;
                                margin-top: -1px;
                                margin-left: 6px;
                            }

                            + .forminator-details-header--tags {
                                margin-top: 10px;
                            }
                        }

                        .forminator-details-header--tags {
                            display: flex;
                            flex-flow: row wrap;

                            + .sui-button {
                                margin-top: $sui-gutter-md;
                            }
                        }

                        @include media(min-width, md) {
                            min-width: 1px;
                            flex: 1;
                        }
                    }

                    @include media(min-width, md) {
                        display: flex;
                        flex-flow: row wrap;
                        text-align: center;
                    }
                }

                @include media(min-width, md) {
                    padding-top: $sui-gutter !important;
                    padding-bottom: $sui-gutter !important;
                }
            }

            .sui-box-body {

                // TAB: Details.
                [id*="tab-content-details-"] {

                    h4, p, li {
                        font-size: 13px;
                        line-height: 22px;
                        font: $font;
                        letter-spacing: $font--letter-spacing;
                    }

                    p, li {
                        color: #888;
                        font-weight: 400;
                    }

                    h4 {
                        margin: 20px 0 0;
                        color: #333;
                        font-weight: bold;

                        &:first-child {
                            margin-top: 0;
                        }
                    }

                    p {
                        margin: 0 0 5px;

                        &:last-child {
                            margin-bottom: 0;
                        }
                    }

                    ol {
                        margin-top: 5px;
                        margin-left: 12px;
                        margin-bottom: 0;
                        padding: 0;
                        border: 0;

                        li {
                            margin: 0;
                            padding: 0;
                            border: 0;
                        }
                    }
                }

                // TAB: Features.
                [id*="tab-content-features-"] {

                    ul {
                        margin: 0;
                        padding: 0;
                        border: 0;

                        li {
                            position: relative;
                            margin: 0;
                            padding: 10px 0 10px 22px;
                            border: 0;
                            color: #333;
                            font: bold 13px/22px $font;
                            letter-spacing: $font--letter-spacing;

                            [class*="sui-icon-"] {
                                position: absolute;
                                top: 15px;
                                left: 0;

                                &:before {
                                    display: block;
                                }
                            }
                        }
                    }
                }

                // TAB: Details.
                [id*="tab-content-changelog-"] {

                    .forminator-addon-changelog {
                        margin: $sui-gutter 0 0;

                        .forminator-addon-changelog--header {
                            display: flex;
                            flex-direction: row wrap;
                            align-items: center;

                            h4 {
                                display: flex;
                                flex: 0 0 auto;
                                flex-direction: row wrap;
                                align-items: center;
                                margin: 0;

                                + p {
                                    margin-left: 10px;
                                }
                            }

                            p {
                                flex: 1;
                                margin: 0;
                                color: #333;
                                font-weight: 500;
                                font-size: 13px;
                                line-height: 22px;
                                letter-spacing: $font--letter-spacing;
                                text-align: right;
                            }
                        }

                        .forminator-addon-changelog--body {
                            margin: 15px 0 0;

                            p {
                                color: #888;
                                font: 400 13px/22px $font;
                                letter-spacing: $font--letter-spacing;
                            }
                        }

                        &:first-child {
                            margin-top: 0;
                        }
                    }
                }
            }
        }
    }
}