@use "sass:math";

@include body-class() {

    #forminator-reports {

        .sui-sidenav {
            .sui-vertical-tabs {
                .sui-vertical-tab {
                    .sui-tag {
                        padding-bottom: 1px;
                    }
                }
            }
        }

        .sui-box__onload {
            .sui-box__message {
                opacity: 1;
                visibility: visible;
                z-index: 1;
                pointer-events: all;
            }
        }

        .sui-box__message {
            display: flex;
			align-items: center;
			justify-content: center;
			position: absolute;
			top: 0;
			right: 0;
			bottom: 0;
			left: 0;
			padding: 5px $sui-gutter-md;
			background-color: rgba(255, 255, 255, 0.7);
            text-align: center;
            opacity: 0;
            visibility: hidden;
            transition: 0.25s ease;
            pointer-events: none;

            p {
                color: palette(gray, light);
				font: 500 12px/22px $font;
				letter-spacing: $font--letter-spacing;

                [class*=sui-icon-] {
                    vertical-align: text-bottom;
                    margin-right: 8px;
                    font-size: 16px;

                    &:before {
						display: block;
						color: palette(gray, light);
					}
                }
            }
        }

        .sui-box-header {
            a {
                [class*="sui-icon-"] {
                    &:before {
                        color: inherit;
                    }
                }
            }
        }

        .sui-box-footer {
            padding: 15px 30px;
            
            strong {
                color: #999;
            }

            p {
                font-weight: $font--weight-bold;
            }
        }

        button {
            &.view-reports {
                @include media(max-width, md) {
                    width: 100%;
                    margin-right: 0;
                }
            }
        }

        .fui-entries-bar {
            .sui-actions-right {

                .sui-form-field {
                    display: flex;
                    
                    label {
                        margin-bottom: 0;
                        margin-right: 10px;
                    }
        
                    .sui-control-with-icon {
        
                        input {
                            font-size: 12px;
                            height: 30px;
                            padding: 7px 12px 7px 29px;
                            min-width: 220px;
        
                            @include media(max-width, md) {
                                min-width: 100%;
                                min-height: 100%;
                            }
                        }
        
                        [class*=sui-icon-] {
                            width: 12px;
                            height: 12px;
                            top: 8px;
                            left: 12px;
                        }
        
                        @include media(max-width, md) {
                            flex: 1;
                        }
                    } 
        
                    @include media(max-width, md) {
                        margin-top: 10px;
                    }
                }
    
                @include media(max-width, md) {
                    display: block;
                    width: 100%;
                }
            }
        }

        .sui-flushed {
            margin-top: -20px;
            
            .sui-table {
                td {
                    padding-top: 20px;
                    padding-bottom: 20px;
                }
                
                .fui-table-title {
                    font-weight: $font--weight-bold;
                    font-size: 12px;
                    line-height: 22px;
                    color: #999;
                    letter-spacing: normal;
                    text-transform: uppercase;
                }
    
                .fui-table-content {
                    display: flex;
                    align-items: center;
                    font-weight: $font--medium;
                    font-size: 20px;
                    line-height: 30px;
                    letter-spacing: -0.25px;
                    color: $nightrider;
                    margin-top: 5px;
                }
            }
        }

        .sui-table-item-title {
            .fui-app--wrapper {
                color: #999;
            }
        }

        .forminator-reports-box {
            .sui-box {
                .sui-box-title {
                    [class*=sui-icon-] {
                        color: $grey;
                    }
                } 
            }
        }
    }

    .daterangepicker.show-ranges {
        .ranges {
            margin-bottom: 20px;
        }
    }
}