@include body-class(true) {

	.sui-insert-variables {

        select.sui-variables {

            + .sui-select {

                .selection {

                    span[role=combobox] {

                        .select2-selection__rendered {
                            padding-left: 0;
                            padding-right: 0;
                        }
                    }
                }
            }
        }
    }    
}